import React, { Dispatch, SetStateAction } from "react"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { IconButton } from "@rp/react/web/mui/core"
import { ZoomOut } from "@rp/react/web/mui/icons"

type PDFViewerHeaderZoomOutButtonProps = {
  scale: number
  setScale: Dispatch<SetStateAction<number>>
  color?: string
}

const PDFViewerHeaderZoomOutButton = ({
  scale,
  setScale,
  color,
}: PDFViewerHeaderZoomOutButtonProps) => {
  return (
    <RPTooltip title="Zoom out" arrow placement="bottom">
      <span>
        <IconButton
          data-testid="zoom-out-button"
          onClick={() => setScale((prevScale: number) => prevScale - 0.2)}
          disabled={scale <= 0.5}
        >
          <ZoomOut sx={{ color }} />
        </IconButton>
      </span>
    </RPTooltip>
  )
}

export { PDFViewerHeaderZoomOutButton }
